body {
background-color: #FDFAF8;
}

#wrapper{
    overflow: hidden;
}

.container {
    width: 100%;
    max-width: 1320px;
    padding: 0 20px;
    margin: 0 auto;
}

.main-btn {
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    bottom: -130px;
    display: inline-flex;
    width: 260px;
    height: 260px;
    padding: 50px;
    border-radius: 50%;
    border: 2px solid $gold;
    letter-spacing: 1px;
    font-weight: 600;
    font-size: 16px;
    text-align: center;
    color: $blue;
    text-transform: uppercase;
    overflow: hidden;
    transition: color 2s;
    z-index: 1;

    @include media('<phone'){
        bottom: -90px;
        width: 180px;
        height: 180px;
        padding: 30px;
        font-size: 14px;
    }

    &::before {
        content:'';
        position: absolute;
        top: var(--y);
        left: calc(var(--x) + 130px);
        transform: translate(-50%, -50%) scale(0);
        width: 260px; 
        height: 260px;
        border-radius: 50%;
        background-color: $gold;
        transition: all .7s ease;
    }

    &:hover{
        color: $white;
        transition: color .3s;

        &::before{
            border: 2px solid $gold;
            transform: translate(-50%, -50%) scale(5);
        }
    }

    i {
        position: relative;
        margin: 0 auto;
        font-style: normal;
        z-index: 1;
    }
}

.line-btn {
    display: inline-flex;
    align-items: center;
    font-family: $gilroy;
    color: $blue;
    font-size: 16px;
    font-weight: 600;
    text-transform: uppercase;
    transition: $all_03;

    @include media('<phone'){
        font-size: 14px;
    }

    &:hover{
        color: $gold;

        img {
            transform: translate(10px);
        }
    }

    img{
        padding-left: 8px;
        transition: $all_03;
    }
}

.stiker {
    position: absolute;
    width: 260px;
    height: 260px;
    border-radius: 50%;
    border: 2px solid $gold;
    padding: 40px;
    transform: rotate(0deg);
    font-family: $gilroy;
    color: $blue;
    font-size: 16px;
    font-weight: 600;
    text-align: center;
    text-transform: uppercase;
    transition: $all_1;

    @include media('<tablet_max'){
        width: 180px;
        height: 180px;
    }

    @include media('<phone'){
        font-size: 14px;
        padding: 30px;
    }
}

.show {
    .stiker {
        transform: rotate(-90deg);
    }
}

.title-wrap {
    position: relative;
    display: inline-block;
    padding: 18px 0 24px 0;
    overflow: hidden;

    @include media('<phone'){
        padding: 14px 0 36px 0;
    }

    &::before {
        content:'';
        position: absolute;
        top: 0;
        left: 0;
        width: 0;
        height: 3px;
        background-color: $gold;
        transition: $all_1;
    }

    .title {
        color: $blue;
        @include adaptiv-vaule("font-size", 80, 42, 1);
        font-weight: 400;
        text-transform: uppercase;
        transition: all .7s ease .7s;
        transform: translateY(-110%);

        br {
            @include media('<phone'){
                display: none;
            }
        }
    }
}

.title-wrap.show{

    &::before {
        width: 100%;
    }

    .title {
        transform: translateY(0);
    }
}

.sec-title-wrap {
    position: relative;
    width: 220%;
    padding: 100px 0 0 0;
    overflow: hidden;
    margin-bottom: 15px;

    @include media('<desktop'){
        width: 100%;
    }

    @include media('<tablet'){
        padding: 56px 0 0 0;
    }

    &::before {
        content:'';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 0;
        height: 3px;
        background-color: $gold;
        transition: all 1.5s linear 0s;
    }

    &::after {
        content:'';
        position: absolute;
        bottom: -191px;
        right: 20%;
        width: 382px;
        height: 382px;
        border-radius: 50%;
        border: 3px solid $gold;
        border-bottom: 3px solid transparent;
        border-left: 3px solid transparent;
        transform: rotate(-225deg);
        transition: all .7s linear 1.5s;

        @include media('<desktop'){
            display: none;
        }
    }

    .sec-title {
        position: relative;
        color: $blue;
        @include adaptiv-vaule("min-height", 80, 42, 1);
        font-weight: 400;
        text-transform: uppercase;
        transform: translateY(100%);
        transition: all 1s ease .7s;
        z-index: 1;
    }
}

.sec-title-wrap.show {

    &::after {
        transform: rotate(-45deg);
    }

    &::before {
        width: 100%;
    }

    .sec-title {
        transform: translate(0);
    }
}

.img-wrap {
    position: relative;
    overflow: hidden;

    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.animate__animated {
    --animate-duration: 1.5s;
}

.animate__fadeIn {
    --animate-duration: 3s;
}
/* --------------------------main-section----------------------- */

.main-section {
    position: relative;
    display: flex;
    align-items: center;
    min-height: 100vh;
    padding: 100px 0 200px 0;
    background-color: rgba($white_2, 0.7);
    overflow: hidden;

    @include media('<phone'){
        min-height: calc(100vh - 55px);
    }

    .ml {
        position: absolute;
        top: -50%;
        height: 200vh;
        width: 165px;
        display: block;
        background-color: $white;
        opacity: 40%;
        z-index: 1;

        @include media('<phone'){
            width: 115px;
        }
    }

    .ml1 {
        left: 50%;
        transform: translate(-115%) rotate(35deg);
        animation: ml1 3s;
    }

    .ml2 {
        right: 50%;
        transform: translate(107%) rotate(35deg);
        animation: ml2 3s;
    }

    @keyframes ml1 {
        0%{
            left: -50%;
        }
        70%{
            left: 66%;
        }
        100%{
            left: 50%;
        }
    }

    @keyframes ml2 {
        0%{
            right: 0;
        }
        70%{
            right: 66%;
        }
        100%{
            right: 50%;
        }
    }

    .container {
        max-width: 900px;
    }

    .main-title {
        font-family: $queensila;
        font-weight: normal;
        @include adaptiv-vaule("font-size", 126, 64, 1);
        text-transform: uppercase;
        color: $blue;
        margin: 0;
        text-align: center;

        @include media('<tablet'){

            br{
                display: none;
            }
        }

        @include media('<phone'){
            text-align: justify;
        }

        i{
            font-style: normal;
            color: $gold;
        }
    }

    .title-line {
        display: inline-block;
        height: 2px;
        min-width: 190px;
        background-color: $gold;
        transform: translateY(-40px);

        @include media('<tablet'){
            min-width: 160px;
            transform: translateY(-25px);
        }
    }

    .main-sub-title {
        text-align: center;
        font-family: $gilroy;
        font-weight: 400;
        @include adaptiv-vaule("font-size", 24, 18, 1);
        text-transform: uppercase;
        color: $gray_2;
        margin: 0;

        @include media('<phone'){
            text-align: left;
        }
    }
}

/* ---------------------------video-section------------------------ */

.video-section {
    position: relative;
    @include adaptiv-vaule("min-height", 750, 260, 1);
    margin-bottom: 80px;

    @include media('<tablet'){
        margin-bottom: 44px;
    }

    video{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        cursor: pointer;
    }
}

/* ----------------------------main-slider-section------------------------ */

.main-slider-section {
    position: relative;
    padding: 80px 0;

    @include media('<tablet'){
        padding: 44px 0;
    }

    .stiker {
        top: 50%;
        right: -170px;
        transform: translateY(-50%) rotate(0deg);

        @include media('<tablet_max'){
            right: -100px;
            @include adaptiv-vaule("top", 500, 175, 1);
            transform: rotate(50deg);
        }
    }

    .container {
        position: relative;
    }

    .bg {
        position: absolute;
        top: 35px;
        left: 0;
        width: 93%;
        height: 80%;
        background-color: #F3F2ED;
        z-index: -1;
        overflow: hidden;

        @include media('<tablet_max'){
            height: 90%;
        }

        img {
            position: absolute;
            right: 0;
            bottom: -60px;

            @include media('<phone'){
                width: 98px;
                height: 90px;
                right: 10px;
                bottom: 0px;
            }
        }
    }

    .swiper-wrapper {
        padding-bottom: 30px;
    }

    .slide {
        opacity: 0 !important;
        z-index: -1 !important;
    }

    .swiper-slide-active {
        opacity: 1 !important;
        z-index: 1 !important;
    }

    .content {
        display: flex;
        padding-right: 75px;

        @include media('<tablet_max'){
            flex-direction: column;
            padding: 0;
        }
    }

    .img-cont {

        @include media('<tablet_max'){
            padding-right: 80px;
            margin-bottom: 36px;
        }
    }

    .text {
        max-width: 300px;
        min-width: 300px;
        margin: 0 0 56px 42px;

        @include media('<tablet_max'){
            max-width: 100%;
            min-width: 0;
            margin: 0 6% 56px 0;
        }
    }

    .description {
        margin-bottom: 20px;

        @include media('<tablet_max'){
            margin-bottom: 36px;
        }

        @include media('<phone'){
            font-size: 14px;
        }
    }

    #pagination {
        display: inline-flex;
        justify-content: flex-end;
        right: 0;
        bottom: 50px;
        padding-right: 100px;

        @include media('<laptop_min'){
            justify-content: center;
            bottom: 0;
            padding: 0;
        }
    }

    .swiper-pagination-bullet {
        width: 83px;
        height: 3px;
        background-color: $gold;
        border-radius: 0;
        margin-left: 20px !important;
        opacity: .3;
        transition: $all_05;

        @include media('<phone'){
            margin-left: 10px !important;
        }

        &:first-child {
            margin-left: 0 !important;
        }

    }

    .swiper-pagination-bullet-active {
        opacity: 1;
    }
}

.show {

    .stiker {
        transform: translateY(-50%) rotate(-90deg);

        @include media('<tablet_max'){
            transform: rotate(-90deg);
        }
    }
}

/* -----------------------certifications-section------------------------ */

.certifications-section {
    padding: 80px 0;

    @include media('<tablet'){
        padding: 44px 0;
    }

    .container {
        text-align: center;
    }

    .title-wrap {
        margin-bottom: 52px;

        @include media('<phone'){
            margin-bottom: 36px;
        }
    }

    .title {
        @include adaptiv-vaule("font-size", 60, 42, 1);
    }

    .content {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
        justify-items: center;
        align-items: center;
        gap: 25px;
    }
}

/* --------------------------------why-section---------------------------- */

.why-section {
    position: relative;
    background: #F6F4F0;
    padding-top: 80px;
    margin-top: 80px;
    text-align: center;
    overflow: hidden;

    @include media('<tablet'){
        padding-top: 56px;
        margin-top: 44px;
    }

    .bg {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);

        @include media('<phone'){
            top: 75%;
        }
    }

    .container {
        position: relative;
        z-index: 1;
    }

    .description {
        max-width: 640px;
        margin: 0 auto 68px auto;

        @include media('<phone'){
            text-align: left;
            margin: 0 0 36px 0;
        }
    }

    .content {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include media('<tablet'){
            flex-direction: column;
            align-items: flex-start;
        }
    }

    .img-cont {
        @include media('<tablet'){
            order: 3;
            margin: 0 auto;
        }
    }

    .list {
        padding: 0;
        margin: 0;
        list-style: none;
        text-align: left;
    }

    .item {
        max-width: 260px;
        padding: 24px 0;
        margin: 0;
        font-family: $gilroy;
        color: $blue;
        font-size: 16px;
        font-weight: 500;
        line-height: 2;
        text-transform: uppercase;

        @include media('<tablet'){
            max-width: 100%;
        }

        @include media('<phone'){
            display: flex;
            align-items: flex-start;
            font-size: 14px;
            letter-spacing: -0.5px;
            line-height: 1.4;
        }
    }

    .number {
        display: block;
        color: $gold;
        font-size: 24px;
        font-weight: 600;
        letter-spacing: -0.66px;

        @include media('<phone'){
            font-size: 22px;
            margin-right: 10px;
            line-height: 1;
        }
    }

    .first {

        .item {
            &:first-child,
            &:last-child {
                padding: 24px 0 30px 24px;

                @include media('<tablet'){
                    padding: 12px 0;
                }

                @include media('<phone'){
                    padding: 9px 0;
                }
            }
        }
    }

    .last {

        .item {
            padding: 24px 0 24px 30px;

            @include media('<tablet'){
                padding: 12px 0;
            }

            @include media('<phone'){
                padding: 9px 0;
            }

            &:first-child,
            &:last-child {
                padding-left: 0;

            }
        }
    }
}

/* ----------------------------cruising-section------------------------ */

.cruising-section {
    position: relative;
    padding: 100px 0;
    margin-bottom: 80px;
    background: #FFFFFF;
    overflow: hidden;

    @include media('<tablet'){
        padding: 56px 0; 
        margin-bottom: 44px;
    }

    .bg {
        position: absolute;
        bottom: -80px;
        left: 50%;
        transform: translate(-50%);

        @include media('<phone'){
            bottom: 0;
        }
    }

    .container {
        position: relative;
        text-align: center;
        z-index: 1;
    }
}

/* ------------------------------sec-slider-section------------------------- */

.sec-slider-section {
    padding: 80px 20px;

    @include media('<tablet'){
        padding: 44px 20px; 
    }

    .container {
        padding: 0;
    }

    .title-wrap {
        margin-bottom: 56px;
    }
    
    .sec-slider {
        max-width: 1320px;
        padding-right: 15px;
        margin: 0 auto;
        overflow: visible;
    }

    .slide {
        min-height: 355px;
        display: flex;
        border: 2px solid #E4E2EA;
        overflow: hidden;
        cursor: grab;

        @include media('<tablet_max'){
            flex-direction: column;
        }
    }

    .img-slide {
        min-width: 245px;
        
        @include media('<tablet_max'){
            min-height: 660px;
        }

        @include media('<phone_max'){
            min-height: 460px;
        }

        @include media('<phone'){
            min-height: 280px;
        }
    }

    .text {
        max-width: 420px;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        padding: 30px;
    }

    .description {
        font-size: 22px;
        font-weight: 400;
        margin-bottom: 24px;
    }

    .user {
        display: flex;
        align-items: center;
    }

    .user-img {
        width: 32px;
        height: 32px;
        border-radius: 50%;
        margin-right: 10px;
    }

    .name {
        font-size: 18px;
        font-weight: 500;
    }

    .slider-nav {
        padding-top: 52px;
    }

    #pagination {
        position: relative;
        bottom: 0;
        margin-bottom: 10px;
        font-family: $gilroy;
        color: #A4A5B1;
        font-size: 14px;
        font-weight: 400;
    }

    #scrollbar {
        position: relative;
        left: 0;
        bottom: 0;
        width: 100%;
        z-index: 1;
        max-width: 1320px;
        padding-right: 20px;
        margin: 0 auto;
    }

    .swiper-scrollbar {
        background: rgba($gold, 0.2);
        border-radius: 0;

    }

    .swiper-scrollbar-drag {
        background: $gold;
        border-radius: 0;
        cursor: grab;
    }
}

/* ----------------------------page-section--------------------- */

.page-section {
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    &::before {
        content:'';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $black;
        opacity: 50%;
    }

    .bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        z-index: -1;
    }

    .title {
        width: 100%;
        max-width: 1320px;
        padding: 0 20px;
        margin: 0 auto;
        text-align: center;
        color: $white;
        @include adaptiv-vaule("font-size", 126, 64, 1);
        font-weight: 400;
        z-index: 1;
    }
}